import React, {useState, useEffect} from 'react';
import ProductModal from './ProductModal.js'
import listService from '../services/listService'
import userService from '../services/userService'

const CollectionTable = ({user}) => {

    const [sortConfig, setSortConfig] = useState({
        key: 'cardNumber',
        direction: 'ascending'
    })
    const [show, setShow] = useState(false)
    const [selectedProduct, setSelectedProduct] = useState({})
    const [collectionEntries, setCollectionEntries] = useState([])

    const getCollectionEntries = (userId) => {
      userService
          .getUser(userId)
          .then(response => {
              setCollectionEntries(response.data.ownedProducts)
          })
    }

    useEffect(() => {
      getCollectionEntries(user.id)
    }, [])

    if(!collectionEntries) {
      return null
    }

    if(collectionEntries.length == 0) {
      return (
        <div>
          You have nothing in your collection! Add items from the "Prices" page.
        </div>
      )
    }
  
    const hideModal = () => {
      setShow(false)
    }

    const requestSort = key => {
        let direction = 'ascending'
        if (sortConfig.key === key && sortConfig.direction === 'ascending') {
          direction = 'descending'
        }

        let newConfig = {
            key: key,
            direction: direction
        }
        setSortConfig(newConfig)
    }

    const DeleteButtonColumn = (productId) => {
      if(!user) {
        return null
      }

      return (
        <td>
          <button onClick={() => deleteProduct(productId)}>
            Delete
          </button>
        </td>
      )
    }

    const IncrementDecrementColumn = (productId) => {
      if(!user) {
        return null
      }

      return (
        <td>
          <button onClick={() => increment(productId)}>
            ^
          </button><br/>
          <button onClick={() => decrement(productId)}>
            v
          </button>
        </td>
      )
    }

    const increment = async (productId) => {
      if(!user) {
        return null
      }

       await listService.increment(user.id, productId)
       getCollectionEntries(user.id)
    }

    const decrement = async (productId) => {
      if(!user) {
        return null
      }

       await listService.decrement(user.id, productId)
       getCollectionEntries(user.id)
    }

    const deleteProduct = async (productId) => {
        if(!user) {
            return null
        }

        await listService.deleteFromCollection(user.id, productId)
        getCollectionEntries(user.id)
    }

    //All-purpose method that can compare string and integer numbers
    //Used to ensure that '10' comes before '2' for example, which doesn't happen with lexicographical comaprison
    //Also handles strings that simply contain numbers (e.g. 1/202)
    const compareNumbers = (a, b) => {
        if(typeof a === 'string' && typeof b === 'string') {
            return a.localeCompare(b, undefined, {numeric: true, sensitivity: 'base'})
        }
        else {
            if (a > b) {
                return 1
            }
            else if(a < b) {
                return -1
            }
            else {
                return 0
            }
        }
    }

    const Difference = ({product}) => {
      if(!product) {
          return null
      }
      let difference = (product.currentMarketPrice - product.previousMarketPrice).toFixed(2)
      let color = 'black'
      if(difference < 0) {
        color = 'red'
      }
      else if(difference > 0) {
        color = 'green'
        difference = '+' + difference
      }

      return (
        <font color={color}>
          {difference}
        </font>
      )
    }

    const PercentDifference = ({product}) => {
      if(!product) {
            return null
      }
      let difference = (((product.currentMarketPrice - product.previousMarketPrice)/product.previousMarketPrice) * 100).toFixed(2)
      let color = 'black'
      if(difference < 0) {
        color = 'red'
      }
      else if(difference > 0) {
        color = 'green'
        difference = '+' + difference
      }

      return (
        <font color={color}>
          {difference}%
        </font>
      )
    }

    const DifferenceFormat = ({value}) => {
        let difference = value.toFixed(2)
        let color = 'black'
        if(difference < 0) {
          color = 'red'
        }
        else if(difference > 0) {
          color = 'green'
          difference = '+' + difference
        }
  
        return (
          <font color={color}>
            {difference}
          </font>
        )
      }
  
      const PercentDifferenceFormat = ({value}) => {
    
        let difference = value.toFixed(2)
        let color = 'black'
        if(difference < 0) {
          color = 'red'
        }
        else if(difference > 0) {
          color = 'green'
          difference = '+' + difference
        }
  
        return (
          <font color={color}>
            {difference}%
          </font>
        )
      }
  

    let sortedCollectionEntries = [...collectionEntries]
    
    if (sortConfig !== null) {

      let aValue = null
      let bValue = null      

      sortedCollectionEntries.sort((a, b) => {  
         
        let aProduct = a.product
        let bProduct = b.product

        if (sortConfig.key === 'difference') {
          aValue = aProduct['currentMarketPrice'] - aProduct['previousMarketPrice']
          bValue = bProduct['currentMarketPrice'] - bProduct['previousMarketPrice']
        }
        else if(sortConfig.key === 'percentDifference') {
          aValue = (aProduct['currentMarketPrice'] - aProduct['previousMarketPrice'])/aProduct['previousMarketPrice']
          bValue = (bProduct['currentMarketPrice'] - bProduct['previousMarketPrice'])/bProduct['previousMarketPrice']
        }
        else {
          aValue = aProduct[sortConfig.key]
          bValue = bProduct[sortConfig.key]
        }

        //Null values (unnumbered cards or empty prices) go to the end regardless of sort order      
        if(aValue === null) {
           //return sortConfig.direction === 'ascending' ? 1 : -1
           return 1
        }
        else if(bValue === null) {
           //return sortConfig.direction === 'ascending' ? -1 : 1
           return -1
        }

        let comparisonResult = compareNumbers(aValue, bValue)
        if (comparisonResult < 0) {
          return sortConfig.direction === 'ascending' ? -1 : 1
        } else if (comparisonResult > 0) {
          return sortConfig.direction === 'ascending' ? 1 : -1
        } else {
        return 0
        }
      })
    }
    


    const calculateTotals = (collectionEntries) => {
        let totalPrice = 0
        let totalPreviousPrice = 0
        let netChange = 0

        for(let k = 0; k < collectionEntries.length; k++) {
            const currentProduct = collectionEntries[k].product
            const currentQuantity = collectionEntries[k].quantity

            totalPrice += currentProduct.currentMarketPrice * currentQuantity
            totalPreviousPrice += currentProduct.previousMarketPrice * currentQuantity
        }

        netChange = totalPrice - totalPreviousPrice
        let percentageChange = netChange / totalPreviousPrice * 100

        return {
            totalPrice,
            netChange,
            percentageChange
        }
    }

    let totals = calculateTotals(sortedCollectionEntries)

    return (
      <div>
        <ProductModal product={selectedProduct} show={show} handleClose={hideModal}/>
        <table class="price-list-table">
          <thead>
            <tr>
              <th>
                <button type="button" onClick={() => requestSort('name')}>
                  Name
                </button>
              </th>
              <th>
                <button type="button" onClick={() => requestSort('cardNumber')}>
                  Number
                </button>
              </th>
              <th>
                <button type="button" onClick={() => requestSort('currentMarketPrice')}>
                  Unit Price
                </button>
              </th>        
              <th>
                <button type="button" onClick={() => requestSort('difference')}>
                  Week Change
                </button>
              </th>
              <th>
                <button type="button" onClick={() => requestSort('percentDifference')}>
                  Week Change (%)
                </button>
              </th>
              <th colspan="2">
                Quantity
              </th>
            </tr>
          </thead>
          <tbody>
            {sortedCollectionEntries.map(collectionEntry => (
              <tr key={collectionEntry.product.productId}>
                <td>
                  <button onClick={() => {
                  setSelectedProduct(collectionEntry.product)
                  setShow(true)
                }}>{collectionEntry.product.name}</button>
                </td>
                <td>{collectionEntry.product.cardNumber}</td>
                <td>{collectionEntry.product.currentMarketPrice}</td>
                <td><Difference product={(collectionEntry.product)}/></td>
                <td><PercentDifference product={(collectionEntry.product)}/></td>
                <td>{collectionEntry.quantity}</td>
                <IncrementDecrementColumn productId={collectionEntry.product._id}/>
                <DeleteButtonColumn productId={collectionEntry.product._id}/>
              </tr>
            ))}
            <tr key={0}>
                <td colspan="2">
                    Totals
                </td>                
                <td>
                    {totals.totalPrice.toFixed(2)}
                </td>
                <td>
                    <DifferenceFormat value={totals.netChange}/>
                </td>
                <td>
                    <PercentDifferenceFormat value={totals.percentageChange}/>
                </td>
            </tr>
          </tbody>
        </table>
      </div>
    )
}

export default CollectionTable
