import React, {useState, useEffect} from 'react'
import priceService from '../services/priceService'
import groupService from '../services/groupService'
import listService from '../services/listService'
import metaService from '../services/metaService'
import PriceTable  from './PriceTable'

import {LOCAL_STORAGE_ID} from '../utils/constants'

const compare = (a, b) => {
  if (a.groupName < b.groupName) {
    return -1;
  }
  if (a.groupName > b.groupName) {
    return 1;
  }
  // a must be equal to b
  return 0;
}

const Prices = () => {
  const [prices, setPrices] = useState([])
  const [groups, setGroups] = useState([])
  const [value, setValue] = useState(2594)
  const [user, setUser] = useState(null)
  const [lastUpdated, setLastUpdated] = useState(null)

  const getAllGroups = () => {
    groupService
        .getAll()
        .then(response => {
          setGroups(response.data.sort(compare))
        })
  }

  const getAllPricesFromSet = (setId) => {
    if(setId == 0) {
      console.log("getting all")
      priceService
        .getAll()
        .then(response => {
          setPrices(response.data)
        })
    }
    else {
      priceService
        .getAllFromSet(setId)
        .then(response => {
          setPrices(response.data)
        })
    }
  }

  useEffect(() => {
      const loggedUserJSON = window.localStorage.getItem(LOCAL_STORAGE_ID)
      if(loggedUserJSON) {
          const user = JSON.parse(loggedUserJSON)
          setUser(user)
          listService.setToken(user.token)
      }
  }, [])

  useEffect(() => {
    getAllPricesFromSet(2594)
    getAllGroups()
    metaService.getLastUpdated().then(response => setLastUpdated(response.data.lastUpdated))
  }, [])

  const changeGroup = (groupId) => {
    getAllPricesFromSet(groupId)
    setValue(groupId)
  }

  let totalMarketValue = 0
  for(let k = 0; k < prices.length; k++) {
    totalMarketValue += prices[k].currentMarketPrice
  }


  return (
    <div align="center">      
      <h1>Current Market Prices</h1>
        <div>
          {user ? "Logged in as: " + user.username : <br/>}<br/><br/>
          Last updated: {lastUpdated}
        </div>   
        <select id="set-selector" name="sets" value={value} onChange={(event) => changeGroup(event.target.value)}>
          <option value={0} key={0}>
            All cards (slow!)
          </option>
          {groups.map(group => 
            <option value={group.groupId} key={group.groupId}>
              {group.groupName}
            </option>
          )}          
        </select>
      <PriceTable user={user} products={prices} isWatchlist={false}/>
    </div>
  )
}

export default Prices