import React, {useEffect, useState} from 'react'
import listService from '../services/listService'
import loginService from '../services/loginService'
import {LOCAL_STORAGE_ID} from '../utils/constants'

const LoginForm = () => {
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [user, setUser] = useState(null)
  
    useEffect(() => {
      const loggedUserJSON = window.localStorage.getItem(LOCAL_STORAGE_ID)
      if(loggedUserJSON) {
          const user = JSON.parse(loggedUserJSON)
          setUser(user)
          listService.setToken(user.token)
      }
    }, [])

    const handleLogin = async (event) => {
        event.preventDefault()
        try {
          const user = await loginService.login({
            username, password,
          })
    
          window.localStorage.setItem(
              LOCAL_STORAGE_ID, JSON.stringify(user)
          )

          listService.setToken(user.token)
          setUser(user)
          setUsername('')
          setPassword('')
        } catch (exception) {
          console.log(exception)
        }
      }

    const logout = () => {
      setUser(null)
      window.localStorage.clear()
    }
     
    const loginForm = () => {
      return (
        <form onSubmit={handleLogin}>
          <div>
            username
              <input type="text" value={username} name="Username" onChange={({ target }) => setUsername(target.value)}/> 
          </div>  
          <div>
            password
              <input type="text" value={password} name="Password" onChange={({ target }) => setPassword(target.value)}/>
          </div>    
          <button type="submit">login</button>
        </form>
      )
    }

    const logoutForm = () => {
      return (
        <div>
          Logged in as {user.username}
          <button onClick={() => logout()}>Logout</button>
        </div>
      )
    }

    return (
      <div>
        {user ? logoutForm() : loginForm()}
      </div>
    )
  }
  
  export default LoginForm