import React, {useState} from 'react'
import ProductModal from './ProductModal.js'
import listService from '../services/listService'

const PriceTable = ({user, products, isWatchlist}) => {

    const [sortConfig, setSortConfig] = useState({
        key: 'cardNumber',
        direction: 'ascending'
    })
    const [show, setShow] = useState(false)
    const [selectedProduct, setSelectedProduct] = useState({})
   
    if(!products) {
      return null
    }
  
    const hideModal = () => {
      setShow(false)
    }

    const requestSort = key => {
        let direction = 'ascending'
        if (sortConfig.key === key && sortConfig.direction === 'ascending') {
          direction = 'descending'
        }

        let newConfig = {
            key: key,
            direction: direction
        }
        setSortConfig(newConfig)
    }

    const AddButtonColumn = (productId) => {
      if(isWatchlist || !user) {
        return null
      }

      return (
        <td>
          <button onClick={() => listService.pushToWatchlist(user.id, productId)}>
            Add to Watchlist
          </button><br/>
          <button onClick={() => listService.pushToCollection(user.id, productId)}>
            Add to Collection
          </button>
        </td>
      )
    }

    const DeleteButtonColumn = (productId) => {
      if(!isWatchlist || !user) {
        return null
      }

      return (
        <td>
          <button onClick={() => listService.deleteFromWatchlist(user.id, productId)}>
            Delete
          </button>
        </td>
      )
    }

    //All-purpose method that can compare string and integer numbers
    //Used to ensure that '10' comes before '2' for example, which doesn't happen with lexicographical comaprison
    //Also handles strings that simply contain numbers (e.g. 1/202)
    const compareNumbers = (a, b) => {
        if(typeof a === 'string' && typeof b === 'string') {
            return a.localeCompare(b, undefined, {numeric: true, sensitivity: 'base'})
        }
        else {
            if (a > b) {
                return 1
            }
            else if(a < b) {
                return -1
            }
            else {
                return 0
            }
        }
    }

    const Difference = ({product}) => {
      let difference = (product.currentMarketPrice - product.previousMarketPrice).toFixed(2)
      let color = 'black'
      if(difference < 0) {
        color = 'red'
      }
      else if(difference > 0) {
        color = 'green'
        difference = '+' + difference
      }

      return (
        <font color={color}>
          {difference}
        </font>
      )
    }

    const PercentDifference = ({product}) => {
      let difference = (((product.currentMarketPrice - product.previousMarketPrice)/product.previousMarketPrice) * 100).toFixed(2)
      let color = 'black'
      if(difference < 0) {
        color = 'red'
      }
      else if(difference > 0) {
        color = 'green'
        difference = '+' + difference
      }

      return (
        <font color={color}>
          {difference}%
        </font>
      )
    }

    let sortedProducts = [...products]
    
    if (sortConfig !== null) {

      let aValue = null
      let bValue = null      

      sortedProducts.sort((a, b) => {  

        if (sortConfig.key === 'difference') {
          aValue = a['currentMarketPrice'] - a['previousMarketPrice']
          bValue = b['currentMarketPrice'] - b['previousMarketPrice']
        }
        else if(sortConfig.key === 'percentDifference') {
          aValue = (a['currentMarketPrice'] - a['previousMarketPrice'])/a['previousMarketPrice']
          bValue = (b['currentMarketPrice'] - b['previousMarketPrice'])/b['previousMarketPrice']
        }
        else {
          aValue = a[sortConfig.key]
          bValue = b[sortConfig.key]
        }

        //Null values (unnumbered cards or empty prices) go to the end regardless of sort order      
        if(aValue === null) {
           //return sortConfig.direction === 'ascending' ? 1 : -1
           return 1
        }
        else if(bValue === null) {
           //return sortConfig.direction === 'ascending' ? -1 : 1
           return -1
        }

        let comparisonResult = compareNumbers(aValue, bValue)
        if (comparisonResult < 0) {
          return sortConfig.direction === 'ascending' ? -1 : 1
        } else if (comparisonResult > 0) {
          return sortConfig.direction === 'ascending' ? 1 : -1
        } else {
        return 0
        }
      })
    }
    
    return (
      <div>        
        <ProductModal show={show} product={selectedProduct} handleClose={hideModal}/>
        <table className="price-list-table">
          <thead>
            <tr>
              <th>
                <button type="button" onClick={() => requestSort('name')}>
                  Name
                </button>
              </th>
              <th>
                <button type="button" onClick={() => requestSort('cardNumber')}>
                  Number
                </button>
              </th>
              <th>
                <button type="button" onClick={() => requestSort('currentMarketPrice')}>
                  Price
                </button>
              </th>        
              <th>
                <button type="button" onClick={() => requestSort('difference')}>
                  Week Change
                </button>
              </th>
              <th>
                <button type="button" onClick={() => requestSort('percentDifference')}>
                  Week Change (%)
                </button>
              </th>
            </tr>
          </thead>
          <tbody>
            {sortedProducts.map(product => (
              <tr key={product.productId}>
                <td>
                  <button onClick={() => {
                  setSelectedProduct(product)
                  setShow(true)
                }}>{product.name}</button>
                </td>
                <td>{product.cardNumber}</td>
                <td>{product.currentMarketPrice}</td>
                <td><Difference product={product}/></td>
                <td><PercentDifference product={product}/></td>
                <AddButtonColumn productId={product._id}/>
                <DeleteButtonColumn productId={product._id}/>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    )
}

export default PriceTable
