import React, {useState, useEffect} from 'react'
import listService from '../services/listService'
import userService from '../services/userService'
import WatchlistTable from './WatchlistTable'
import {LOCAL_STORAGE_ID} from '../utils/constants'

const Watchlist = () => {
    const [user, setUser] = useState(null)
    const [watchlist, setWatchlist] = useState([])

    const getWatchlist = (userId) => {
        userService
            .getUser(userId)
            .then(response => {
                setWatchlist(response.data.watchlist)
            })
      }

    useEffect(() => {
        const loggedUserJSON = window.localStorage.getItem(LOCAL_STORAGE_ID)
        if(loggedUserJSON) {
            const user = JSON.parse(loggedUserJSON)
            setUser(user)
            getWatchlist(user.id)
            listService.setToken(user.token)
        }
    }, [])

    const watchlistTable = () => {
        return <WatchlistTable user={user} watchlistEntries={watchlist}/>
    }

    return (
        <div align="center">
            <h1>Watchlist</h1>
            {user ? watchlistTable() :
            <div>
                Log in first to see your watchlist!
            </div>}
        </div>
    )
}

export default Watchlist